body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.darktheme {
  background: #000;
  background-size: contain;
}

.darktheme h1 {
  color: #fff;
}

.container {
  max-width: 960px;
  margin: 0 auto;
  padding: 0 15px;
  min-height: 100vh;
}

.notes-list {
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}

.note.new {
  background-color: #628E90;
}

textarea {
  border: none;
  resize: none;
  background-color: #628E90;
  color: black;
}

textarea::placeholder {
  color: #000;
}

textarea:focus {
  outline: none;
}

.save {
  background-color: #F1EEE9;
  border: none;
  border-radius: 15px;
  padding: 5px 10px;
  size: 1.3em;
}

.save:hover {
  background-color: #fff;
  cursor: pointer;
}

.note-container {
  display: flex;
  flex-direction: column;
  gap: .5rem;
}

.note {
  background-color: #FEF68A;
  margin-bottom: 10px;
  border-radius: 10px;
  padding: 1rem;
  min-height: 190px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  white-space: pre-wrap;
}

#title {
  margin: 0;
}

.note-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.delete-icon {
  cursor: pointer;
  size: 1.3em;
}

.search {
  display: flex;
  align-items: center;
  background-color: #EEEEEE;
  border-radius: 10px;
  padding: 5px;
  margin-bottom: 1.2em;
}

.search input {
  border: none;
  background-color: #EEEEEE;
}

.search input:focus {
  outline: none;
}